.model-tile-container {
  position: relative;
  display: inline-block;
  flex: 0 0 48%;
  margin-bottom: 4%;
}

.model-tile-image {
  width: 100%;
  height: auto;
  display: block;
}

.outer-circle {
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 3px solid #246bfd;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-top: 10px;
  margin-right: 10px;
}

.inner-circle {
  display: none;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #246bfd;
}
