.camera-container {
  position: relative;
  width: 100%; /* This will make the camera feed take up 80% of its parent container's width */
  margin: auto; /* This centers the camera feed horizontally */
  aspect-ratio: 1/1; /* This makes the camera feed square */
}

/* Add styles for the button */
.camera-button {
  display: block;
  width: 200px; /* Set a fixed width for the button */
  padding: 10px;
  margin: 20px auto; /* This centers the button horizontally and adds space above it */
  cursor: pointer;
  /* Add other styles for the button here */
}

#camera {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; /* Make the overlay match the size of the video feed */
  height: 100%; /* Set the height to cover the video element */
  z-index: 10; /* Ensure the overlay is above the video feed */
}
