.time-button {
  width: 31%;
  padding: 2.6% 0 2.6% 0;
  margin-bottom: 4.2%;
  color: #1c646e;
  font-size: 18px;
  font-weight: 700;
  background-color: #ffffff;
  border: 2px solid #1c646e;
  border-radius: 100px;
}

.time-button,
.time-picker-label {
  cursor: pointer;
}

.time-button.selected {
  background-color: #1c646e;
  color: #ffffff;
}
