.scan-complete-container,
.body-scanner-container {
  width: 100%;
  display: grid;
  place-items: center;
}

.loading-text,
.prompt-text {
  font-size: 16px;
  font-weight: 400;
}

.view-text {
  font-size: 24px;
  font-weight: 700;
  color: #246bfd;
}

.image {
  width: 70%;
  height: auto;
}

.scan-complete-text {
  font-size: 24px;
  font-weight: 700;
  color: #363457;
}

.scan-complete-button {
  color: #8e8e8e;
  font-weight: 700;
  background-color: #ffffff;
  border-width: 0;
  padding: 10px 16px 10px 16px;
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 2px 4px 8px 1px;
  margin-bottom: 5%;
}

.loading-spinner {
  position: relative;
  translate: 50%;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background:
    radial-gradient(farthest-side, #bd00ff 94%, #0000) top/9px 9px no-repeat,
    conic-gradient(#0000 30%, #bd00ff);
  -webkit-mask: radial-gradient(farthest-side, #0000 calc(100% - 9px), #000 0);
  animation: spinner-c7wet2 1s infinite linear;
}

@keyframes spinner-c7wet2 {
  100% {
    transform: rotate(1turn);
  }
}
