.radio-switch-container {
  display: flex;
  border-radius: 24px;
  margin: 20px 0px 20px 0px;
  padding: 20px;
  cursor: pointer;
  box-shadow: 0px 4px 60px rgba(4, 6, 15, 0.05);
}

.image-text-container {
  display: flex;
  flex-grow: 1;
}

.switch-container {
  flex-grow: 0;
  display: flex;
  margin-left: auto;
}

.radio-image {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  pointer-events: none;
}

.radio-switch-text {
  padding-left: 20px;
  font-weight: 700;
  font-size: 16px;
}

.switch-label {
  position: relative;
  display: block;
  width: 56px;
  height: 30px;
  margin: auto;
}

.switch-input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 24px;
  width: 24px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.switch-input:checked + label {
  background-color: #1c646e;
}

.switch-input:checked + .slider {
  background-color: #1c646e;
}

.switch-input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.radio-switch-container.selected {
  background-color: black;
}
