.pp-container {
  position: relative;
  display: flex;
  width: 100%;
  box-shadow: 0 40px 120px rgba(31, 31, 25, 0.6);
  border-radius: 8px;
  aspect-ratio: 1/1;
}

.before-image,
.after-image {
  border-radius: 8px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  z-index: 1;
}

.before-image {
  position: absolute;
  inset: 0;
}

.slider-line {
  position: absolute;
  top: 0;
  height: 100%;
  width: 1.6%;
  translate: -50%;
  background: #ffffff;
  cursor: ew-resize;
  touch-action: none;
  z-index: 2;
}

.slider-icon {
  position: absolute;
  top: 50%;
  transform: translate(-46%, -50%);
  width: 1050%;
  height: auto;
  cursor: ew-resize;
}

.slider-input {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0;
}

.before-label,
.after-label,
.credential {
  position: absolute;
  color: #fff;
  transition: opacity 1s ease;
  z-index: 3;
}

.before-label,
.after-label {
  font-size: 24px;
  font-weight: 700;
  left: 50%;
  top: 3%;
  transform: translate(-50%, -50%);
  font-size: 20px;
}

.credential {
  color: #cacaca;
  right: 3%;
  bottom: -2%;
  font-size: 16px;
  font-weight: 700;
  z-index: 3;
}
